'use strict';

import { ensureMkForLatestMessage } from '../core/key/mk';
import { initializeExternalSettings } from '../initer/auto/settings';
import { ensurePlaceholder } from '../macro/placeholder';
import { logContext, Logger } from '../utils/log';
import { getMessageContentWithRetry, isUserMessage } from '../utils/message';
import { handleApiEvent } from './handlers/api/dispatcher';
import { handleSyncEvent } from './handlers/sync';
import { handleUpdateMkOnlyEvent } from './handlers/updateMkOnly';
import { EventJob, getEventGroup } from './merger';
import { ActionsTaken, DispatcherPayload } from './types';

const logger = new Logger();

interface EventContextPayload {
  type: string;
  detail?: any;
}

function extractMessageIdFromDetail(detail: any): number | null {
  if (detail === null || detail === undefined) return null;

  if (typeof detail === 'number') {
    if (Number.isFinite(detail)) {
      logger.debug('extractMessageIdFromDetail', ' number detail  message_id', { message_id: detail });
      return detail;
    }
    return null;
  }

  if (typeof detail === 'string') {
    const parsed = Number(detail);
    if (Number.isFinite(parsed)) {
      logger.debug('extractMessageIdFromDetail', ' string detail  message_id', { raw: detail, message_id: parsed });
      return parsed;
    }
    return null;
  }

  if (Array.isArray(detail) && detail.length > 0) {
    const parsed = Number(detail[0]);
    if (Number.isFinite(parsed)) {
      logger.debug('extractMessageIdFromDetail', ' detail[0]  message_id', { raw: detail[0], message_id: parsed });
      return parsed;
    }
    return null;
  }

  if (typeof detail === 'object') {
    if (typeof detail.message_id === 'number') {
      if (Number.isFinite(detail.message_id)) {
        logger.debug('extractMessageIdFromDetail', ' detail.message_id(number)  message_id', {
          message_id: detail.message_id,
        });
        return detail.message_id;
      }
      return null;
    }
    if (typeof detail.message_id === 'string') {
      const parsed = Number(detail.message_id);
      if (Number.isFinite(parsed)) {
        logger.debug('extractMessageIdFromDetail', ' detail.message_id(string)  message_id', {
          raw: detail.message_id,
          message_id: parsed,
        });
        return parsed;
      }
      return null;
    }
    if (Array.isArray(detail.eventArgs) && detail.eventArgs.length > 0) {
      const parsed = Number(detail.eventArgs[0]);
      if (Number.isFinite(parsed)) {
        logger.debug('extractMessageIdFromDetail', ' detail.eventArgs[0]  message_id', {
          raw: detail.eventArgs[0],
          message_id: parsed,
        });
        return parsed;
      }
      return null;
    }
  }

  logger.debug('extractMessageIdFromDetail', ' detail  message_id', { detail });
  return null;
}

/**
 * @constant {number} RENDER_EVENTS_TO_IGNORE_AFTER_MK_INJECTION
 * @description  `ensureMessageKey`  MK  `character_message_rendered` 
 *  1
 */
const RENDER_EVENTS_TO_IGNORE_AFTER_MK_INJECTION = 1;

/**
 * @interface IgnoreRule
 * @description  MK 
 */
export interface IgnoreRule {
  mk: string;
  ignoreCount: number;
}

/**
 * @interface ConsecutiveMkState
 * @description  MK 
 */
export interface ConsecutiveMkState {
  mk: string | null;
  count: number;
}

/**
 * @var {ConsecutiveMkState | null} consecutiveMkState
 * @description  MK 
 * ****:  MK
 */
let consecutiveMkState: ConsecutiveMkState | null = null;

/**
 * ** MK **
 * @param eventType - 
 * @param currentMk -  MK
 * @param mkToIgnore - 
 * @returns {{ shouldSkip: boolean; newIgnoreRule: IgnoreRule | null }} - 
 */
function handleRedundantRenderEvent(
  eventType: string,
  currentMk: string | null,
  mkToIgnore: IgnoreRule | null,
): { shouldSkip: boolean; newIgnoreRule: IgnoreRule | null } {
  if (mkToIgnore && eventType === tavern_events.CHARACTER_MESSAGE_RENDERED && currentMk === mkToIgnore.mk) {
    logger.debug(
      'handleRedundantRenderEvent',
      ` MK (${mkToIgnore.mk}) : ${mkToIgnore.ignoreCount - 1}`,
    );
    mkToIgnore.ignoreCount--;
    if (mkToIgnore.ignoreCount <= 0) {
      mkToIgnore = null; // 
      logger.debug('handleRedundantRenderEvent', ``);
    }
    return { shouldSkip: true, newIgnoreRule: mkToIgnore };
  }
  return { shouldSkip: false, newIgnoreRule: mkToIgnore };
}

/**
 * ****
 * @returns {number} - 
 */
function updateConsecutiveMkCount(): number {
  const mk = logContext.mk;
  if (mk && consecutiveMkState && consecutiveMkState.mk === mk) {
    logger.debug(
      'updateConsecutiveMkCount',
      `/ MK: ${mk}: ${consecutiveMkState.count}: ${consecutiveMkState.count + 1}`,
    );
    consecutiveMkState.count++;
  } else {
    logger.debug(
      'updateConsecutiveMkCount',
      `/ MK: ${mk} 1 MK : ${consecutiveMkState?.mk}`,
    );
    consecutiveMkState = { mk: mk, count: 1 };
  }
  return consecutiveMkState.count;
}

/**
 * ****
 * 
 * @param {EventJob} job - 
 * @param {IgnoreRule | null} mkToIgnore - ****:  (event queue processing loop) 
 * @returns {Promise<IgnoreRule | null>} - 
 */
export async function dispatchAndExecuteTask(
  job: EventJob,
  mkToIgnore: IgnoreRule | null,
  eventContext?: EventContextPayload,
): Promise<IgnoreRule | null> {
  const { type: eventType } = job;
  const eventGroup = getEventGroup(eventType);
  let message_id: number | null = null;
  const eventDetail = eventContext?.detail ?? job.detail;

  // 
  const actionsTaken: ActionsTaken = { rollback: false, apply: false, resync: false, api: false, apiWrite: false };

  try {
    // ****: 
    // 
    await getMessageContentWithRetry();

    const latestMessage = getChatMessages(-1, { include_swipes: true })?.[0];
    if (!latestMessage) {
      logger.warn('dispatchAndExecuteTask', '');
      return mkToIgnore;
    }

    // ****:  MK 
    logger.log('dispatchAndExecuteTask', ' ensureMkForLatestMessage');
    const { mk, message_id: msgId, isNewKey } = await ensureMkForLatestMessage(latestMessage);
    if (!mk || msgId === null) {
      logger.warn('dispatchAndExecuteTask', ' MK  ID');
      return mkToIgnore;
    }
    logContext.mk = mk;
    message_id = msgId;

    const is_user = isUserMessage(latestMessage);

    // ****:  AI 
    logger.log('dispatchAndExecuteTask', ' ensurePlaceholder');
    await ensurePlaceholder(message_id);

    //  ensureMkForLatestMessage  MK
    if (isNewKey && mk) {
      mkToIgnore = {
        mk: mk,
        ignoreCount: RENDER_EVENTS_TO_IGNORE_AFTER_MK_INJECTION,
      };
    }

    // ****:  MK 
    logger.debug('dispatchAndExecuteTask', ' handleRedundantRenderEvent');
    const { shouldSkip, newIgnoreRule } = handleRedundantRenderEvent(eventType, mk, mkToIgnore);
    mkToIgnore = newIgnoreRule; // 
    if (shouldSkip) {
      // 
      return mkToIgnore;
    }

    logger.log('dispatchAndExecuteTask', `: ${eventType} (: ${eventGroup})`);

    // ****
    // v3.1 payload 
    const payload: DispatcherPayload = {
      mk: mk,
      message_id: message_id,
      is_user: is_user,
      actions: actionsTaken,
      consecutiveProcessingCount: 1, // 
    };

    switch (eventGroup) {
      case 'INIT':
        logger.log('dispatchAndExecuteTask', ' initializeExternalSettings');
        initializeExternalSettings();
        // swipe
        payload.consecutiveProcessingCount = updateConsecutiveMkCount();
        logger.log('dispatchAndExecuteTask', ' handleSyncEvent for INIT');
        await handleSyncEvent(job, actionsTaken, payload);
        break;
      case 'SYNC':
        payload.consecutiveProcessingCount = updateConsecutiveMkCount();
        logger.log('dispatchAndExecuteTask', ' handleSyncEvent for SYNC');
        await handleSyncEvent(job, actionsTaken, payload);
        break;
      case 'API':
        logger.log('dispatchAndExecuteTask', ' handleApiEvent');
        handleApiEvent(job, actionsTaken, payload);
        break;
      case 'UPDATE_MK_ONLY':
        logger.log('dispatchAndExecuteTask', ' handleUpdateMkOnlyEvent');
        await handleUpdateMkOnlyEvent();
        break;
      case 'DIRECTED_SYNC': {
        payload.consecutiveProcessingCount = updateConsecutiveMkCount();
        const targetedMessageId = extractMessageIdFromDetail(eventDetail);
        if (targetedMessageId === null) {
          logger.warn('dispatchAndExecuteTask', ' message_id', { eventDetail });
          await handleSyncEvent(job, actionsTaken, payload);
          break;
        }
        logger.log(
          'dispatchAndExecuteTask',
          ` handleSyncEvent for DIRECTED_SYNC (message_id: ${targetedMessageId})`,
        );
        await handleSyncEvent(job, actionsTaken, payload, targetedMessageId);
        break;
      }
    }
  } catch (error) {
    logger.error('dispatchAndExecuteTask', ` ${eventType} : ${error}`, error);
  } finally {
    // 
    logContext.mk = '';

    // ****: 
    //
    //await new Promise(resolve => setTimeout(resolve, 50));
  }

  return mkToIgnore;
}
